.popup {
  background-color:  rgba(0, 0, 0, 0.8);
  position: fixed;
  overflow: hidden;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.4);
  justify-content: center;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  align-items: center;
  display: flex;
  opacity: 0;
  transition: 1s;
  z-index: -1;
}
