.popup__picture {
  object-fit: cover;
  width: 75vw;
  height: 75vh;
  color: black;
}

@media all and (max-width: 625px) {
  .popup__picture {
    width: 433px;
    height: 540px;
  }
}

@media all and (max-width: 450px) {
  .popup__picture {
    width: 240px;
    height: 299px;
  }
}
